#stats {
  position: absolute;
  top: 570px;
  display: flex;
  justify-content: space-evenly;
  left: 50%;
  transform: translateX(-50%);
}
.statsContainer {
  border: 1px solid #38384f;
  width: 220px;
}
.nameOfStats {
  color: #838391;
  font-family: "Spartan", sans-serif;
  font-size: 11px;
  letter-spacing: 1px;
  margin-left: 20px;
  margin-top: 20px;
}
.statsReport {
  font-family: "Antonio", sans-serif;
  font-size: 40px;
  letter-spacing: -1.5px;
  line-height: 52px;
  color: white;
  margin-left: 20px;
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  #stats {
    top: 900px;
  }
  .statsReport {
    font-size: 24px;
  }
  .nameOfStats {
    font-size: 8px;
  }
}

@media (max-width: 541px) {
  #stats {
    display: none;
    top: 700px;
    flex-wrap: wrap;
  }
}
