#image {
  height: 290px;
}

@media (max-width: 768px) {
  #image {
    /* this element is positioned relative to the root element */
    position: absolute;
    left: 258px;
    top: 272px;
    width: 253px;
    height: 253px;
  }
}

@media (max-width: 541px) {
  #image {
    width: 111px;
    height: 111px;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
  }
}
