#navbarNavAltMarkup {
  justify-content: flex-end;
}
.navbar-brand {
  font-family: "Antonio", sans-serif;
  font-size: 40px;
  letter-spacing: -1.5px;
  line-height: 52px;
}
.navbar {
  color: white;
  border-bottom: 1px solid #838391;
}
.nav-link {
  font-family: "Spartan", sans-serif;
  letter-spacing: 1px;
  line-height: 25px;
  font-weight: bold;
  font-size: 11px;
  color: white;
  cursor: pointer;
}

@media (max-width: 768px) {
  /* tablet view */
  .navbar {
    display: block;
    text-align: center;
  }
  #navbarNavAltMarkup {
    justify-content: space-evenly;
  }
  .navbar-brand {
    font-size: 28px;
  }
}

@media (max-width: 541px) {
  .navbar-toggler-icon {
    color: white;
  }
  .navbar {
    height: 100%;
    display: flex;
    justify-content: space-between;
  }
}
