#descriptionContainer {
  width: 350px;
}
#nameOfPlanet {
  color: white;
  font-family: "Antonio", sans-serif;
  font-size: 80px;
  line-height: 103px;
  text-transform: uppercase;
  margin: 0;
}
#descriptionOfPlanet {
  color: white;
  font-size: 14px;
  font-family: "Spartan", sans-serif;
  line-height: 25px;
  margin: 25px 0;
}

#source,
#wikiPage {
  color: #838391;
  font-size: 14px;
  font-family: "Spartan", sans-serif;
}

#wikiPage {
  text-decoration: underline;
}

#source {
  margin-right: 5px;
}

#wikiPageIcon {
  margin-left: 5px;
}

@media (max-width: 768px) {
  #nameOfPlanet {
    font-size: 48px;
  }
  #descriptionContainer {
    /* this element is positioned relative to the root element */
    position: absolute;
    width: 339px;
    height: auto;
    left: 39px;
    top: 620px;
  }
  #descriptionOfPlanet {
    font-size: 11px;
    line-height: 22px;
    margin: 0 0 8px 0;
  }
  #source,
  #wikiPage {
    font-size: 12px;
  }
}

@media (max-width: 541px) {
  #descriptionContainer {
    top: 200px;
    width: 100%;
    left: 0;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
  }
}
