#contentBody {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 768px) {
  #contentBody {
    display: block;
    position: static;
    transform: none;
  }
  #moreDescription {
    /* this element is positioned relative to the body root element */
    position: absolute;
    width: 281px;
    height: 152px;
    left: 447px;
    top: 676px;
  }
}

@media (max-width: 541px) {
  #contentBody {
    display: none;
    position: relative;
    top: 0;
    left: 0;
    height: 1200px;
  }
  #moreDescription {
    top: 450px;
    left: 50%;
    transform: translateX(-50%);
  }
}
