.moreInfoOnPlanet {
  border: 1px solid #38384f;
  margin-top: 10px;
  font-family: "Spartan", sans-serif;
  font-size: 11px;
  letter-spacing: 1px;
  line-height: 25px;
  cursor: pointer;
  height: 40px;
  display: flex;
  align-items: center;
}
.moreInfoOnPlanet:hover {
  background-color: #38384f;
}

.number {
  color: #838391;
  margin: 0 20px;
}
.name {
  color: white;
}

@media (max-width: 768px) {
  .moreInfoOnPlanet {
    font-size: 9px;
  }
}
